body {
	font-family: Arial;
}

.whiteboard__canvas {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
}

.seats {
	font-size: 1rem;
	width: 20%;
	background: rgba(0, 0, 0, 0.25);
	height: 100%;
	position: absolute;
	top: 0;
	right: 0;
	padding: 1rem;
	z-index: 10;
}

.seat__user-color {
	width: 10px;
	height: 10px;
	display: inline-block;
	margin-right: 5px;
}

.seat__user-name {
	margin-right: 5px;
}

.seat__user-status {
	font-size: 0.8rem;
}


